import React, {useEffect, useState} from 'react'
import './index.css'
import {useHttp} from "../../hooks/http.hook";
import {Hearts} from "react-loader-spinner";
import Select from "react-select";

function Staff(){
    const {request, loading} = useHttp()
    const [allStaff, setAllStaff]=useState({
        error:null,
        isLoad:false,
        items:[],
        data:''
    })
    const [payment, setPayment] = useState(null)
    const [worker_id, setWorker_id] = useState(null)
    const [role, setRole] = useState(null)
    const options = [
        {value:'USER', label:'USER'},
        {value:'ADMIN', label:'ADMIN'}
    ]

    useEffect(() => {
        async function fetchStaff(){
            const response = await request('/crm-frontend/allstaff','POST')
            console.log(response);
            setAllStaff({data: response})
        }
        fetchStaff()
    },[])

    const roleHandler_setRole = (event) => {
        setRole(event.value)
    }
    const roleHandler_setWorkerId = event => {
        setWorker_id(event.target.closest('td').childNodes[0].childNodes[1].textContent)
    }
    const paymentHandler_setPayment_setWorkerId = event => {
        setPayment(event.target.value)
        setWorker_id(event.target.closest('td').childNodes[2].textContent)
    }

    const sendNewPaymentAtServer = async () => {
        const obj = { worker_id, payment }
        const response = await request('/setPayment','POST', obj)
    }
    const sendNewRoleAtServer = async () => {
        const obj = {worker_id,role}
        const response = await request('/setRole','POST', obj)
    }

    function returnTable(){
        return(
            <table className=" table-hover tableStaff">
                <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th>  , </th>
                    <th></th>
                </tr>
                </thead>
                {
                    allStaff.data.map(e=>{
                        return(
                            <tbody>
                            <tr>
                                <td>{e.name}</td>
                                <td>{e.job}</td>
                                <td className="inline_twoElements">
                                    <input type="number" onChange={paymentHandler_setPayment_setWorkerId} placeholder={e.paymentPerHour}/>
                                    <div onClick={sendNewPaymentAtServer} className="btnSetNewPayment">ok</div>
                                    <div className="hidden">{e._id}</div>
                                </td>
                                <td >
                                    <div className=" inline_twoElements">
                                        <div onClick={roleHandler_setWorkerId}>
                                            <Select  className="select_staff" placeholder={e.roles} onChange={roleHandler_setRole} options={options}/>
                                        </div>
                                        <div className="none">{e.roles_id}</div>
                                        <div onClick={sendNewRoleAtServer} className="btnSetNewPayment">ok</div>
                                    </div>
                                </td>
                            </tr>
                            </tbody>
                        )
                    })
                }
            </table>
        )
    }

    return(
        <div className="mainBox">
            {!allStaff.data
                ?<Hearts
                    className="loaderBoxFc"
                    type="Puff"
                    color="#00BFFF"
                    height={45}
                    width={45}
                />
                :returnTable()
            }
        </div>
    )
}
export default Staff