import React, {useEffect, useState} from "react";
import Select from 'react-select'
import { ToastContainer, toast } from 'react-toastify';
import {useHttp} from "../../hooks/http.hook";
import './newDogovor.css'

let options = []

function NewDogovor() {
    const {error, request, clearError} = useHttp()
    const [allStaff, setAllStaff] = useState(null)

    const [data, setData] = useState({
        company: '',
        contactFace: '',
        address: '',
        totalSum: '',
        phone: '',
        additional: '',
        dogovor_date: '',
        workers:''
    })

    useEffect(()=>{
        if(error===null){

        }else{
            toast(` Wow so easy! ${error}`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
        clearError()
    },[error, clearError])
    useEffect(() => {
        async function fetchStaff(){
            const staff = await request('/allstaff', 'POST')
            console.log(staff)
            setAllStaff(staff)
        }
        fetchStaff()
    },[])
    useEffect(() => {
        console.log('allStaff   ...  : ', allStaff)
        allStaff==null
            ? console.log('allStaff  : ')
            :updateOptions()
    },[allStaff])

    const saveDogovorAtDatabase = async () => {
        console.log('data: ',data)
        try {
            console.log('contact has been saved')
            const datafromserver = await request('/newcontract','POST',{...data})
            console.log('data from server: ', datafromserver)
            toast(` ${datafromserver.message}`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } catch (e){}
    }

    function updateOptions(){
        options=[]
        allStaff.map(e=>{
            let obj={
                value:e.name,
                label:e.name,
                id:e._id
            }
            options.push(obj)
        })
    }
    function returnSelectorsList_Staff(){
        return(
            <div className="mb-3 regDiv">
                <label className="form-label" htmlFor="selection"> </label>
                <Select onBlur={(e)=>{checkWorkersValue()}} onChange={changeHandlerWorkers} isMulti options={options} id="selection"/>
            </div>
        )
    }

    const changeHandler = event => {
        setData({...data, [event.target.name]: event.target.value})
    }
    const changeHandlerWorkers = event => {
        console.log(typeof event)
        setData({...data, workers: event})

    }
    const checkWorkersValue = () => {
        console.log('log inside checkWorkersValue!    !')
        console.log(data.workers)

        // let arrayWorkers=[]
        // for(let e of data.workers){
        //     arrayWorkers.push(e.label)
        // }
        // let workersString=arrayWorkers.join(',')
        // setData({...data,workers:workersString})
        //
        // console.log('data.workers inside checkWorkersValue: ',data.workers)
    }

    return (
        <div>
            <ToastContainer/>
                <div id='visabilityContent'>
                    <div className="mainDiv">
                        <div className="myform">
                            <div className="divForTwoBlock">
                                <div className="leftDiv">
                                    <div className="mb-3 regDiv">
                                        <label className="form-label" htmlFor="company_name"></label>
                                        <input onChange={changeHandler} name="company" id="company_name" type="text" className="form-control"/>
                                        </div>
                                    <div className="mb-3 regDiv">
                                            <label className="form-label" htmlFor="contact_face"> </label>
                                            <input onChange={changeHandler} name="contactFace" id="contact_face" type="text" className="form-control"/>
                                        </div>
                                    <div className="mb-3 regDiv">
                                            <label className="form-label" htmlFor="address"></label>
                                            <input onChange={changeHandler} name="address" id="address" type="text" className="form-control"/>
                                        </div>
                                    <div className="mb-3 regDiv">
                                            <label className="form-label" htmlFor="phone"></label>
                                            <input onChange={changeHandler} name="phone" id="phone" type="text" className="form-control"/>
                                        </div>
                                </div>
                                <div className="rightDiv">
                                        {returnSelectorsList_Staff()}
                                        <div className="mb-3 regDiv">
                                            <label className="form-label" htmlFor="dogovor_date"> /  </label>
                                            <input onChange={changeHandler} name="dogovor_date" id="dogovor_date" type="date" className="form-control"/>
                                        </div>
                                        <div className="mb-3 regDiv">
                                            <label className="form-label" htmlFor="additional_info"> </label>
                                            <input onChange={changeHandler} name="additional" id="additional_info" type="text" className="form-control"/>
                                        </div>
                                        <div className="mb-3 regDiv">
                                            <label className="form-label" htmlFor="total_sum"> </label>
                                            <input onChange={changeHandler} name="totalSum" id="total_sum" type="text" className="form-control"/>
                                        </div>
                                </div>
                            </div>
                            <a onClick={saveDogovorAtDatabase} className="btn btn-primary">   </a>
                        </div>
                    </div>
                </div>
        </div>
    )
}
export default NewDogovor